.profile-box {
    align-items: center;
    justify-content: center;
    padding: 1rem 1rem 0 0;
    border-style: none solid none none;
    border-color: #2c384e;
    border-width: 2px;
    height: 100%;
}

@media screen and (max-width: 768px) {
    .profile-box {border: none;}
}

.loading-box {
    min-height: 20rem;
    padding-top: 1rem;
}